/*** GDPR Styles ***/
.gdpr-card {
  color: #000;
  font-size: 10px;
  font-family: sans-serif;
  position: relative;
  margin: 15px 0;
  padding-left: 64px;
  &:before {
    content: "";
    background-color: #00349a;
    // background-image: url("../../img/img-gdpr.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    border-radius: 4px 0 0 4px;
    display: inline-block;
    position: absolute;
    width: 64px;
    height: 100%;
    left: 0;
    top: 0;
  }
  & > div {
    background: #fff;
    border: 1px solid #ddd;
    border-radius: 0 4px 4px 0;
    border-left-width: 0;
    text-align: left;
    padding: 12px;
  }
  & strong {
    display: block;
    font-weight: bold;
    margin-bottom: 3px;
  }
  & strong + div {
    color: #666;
  }
  & a {
    color: #222;
    text-decoration: underline;
  }
  html[dir="RTL"] & {
    padding-left: 0;
    padding-right: 64px;
    &:before {
      border-radius: 0 4px 4px 0;
      left: auto;
      right: 0;
    }
    & > div {
      text-align: right;
      border-radius: 4px 0 0 4px;
      border-right-width: 0;
      border-left-width: 1px;
    }
  }
  @media only screen and (max-width: 418px) {
    font-size: 10px;
    padding-left: 0;
    padding-top: 42px;
    &:before {
      width: 100%;
      height: 42px;
      background-size: 35px auto;
      border-radius: 4px 4px 0 0;
    }
    & > div {
      border-radius: 0 0 4px 4px;
      border-left-width: 1px;
      border-top-width: 0;
      padding: 10px;
    }
  }
}

